var overviewList=[{'icon':__webpack_require__(887),'title':'走进新县','details':[{'type':'image','value':__webpack_require__(888)},{'type':'author','value':'管理员发布 January 21, 2015'},{'type':'content','value':['新县伴随中国新民主主义革命进程而诞生，素有“红色首府、将军故里、诗画江南”之美誉。','新县位于河南省信阳市东南部，被称为“中原南门”。新县位于河南省信阳市东南部、大别山腹地、鄂豫两省交接地带。地理坐标东经114°33′～115°12′，北纬31°28′～31°46′。东与商城县接壤，南与湖北省麻城市、红安县毗邻，西与罗山县及湖北省大悟县交界，北与光山县相连。东西长61.6公里，南北宽40.7公里，全县总面积1612平方公里（241.8万亩）。新县是中原通向江南的要冲,大别山雄踞其间,扼守中原南大门,俯瞰荆楚大地，自古为战略要地。历史上大别山区有13个著名关隘，新县境内就有墨斗、白沙、东黄土、木陵4个，另有虎头、阴山、土门、修善4个重要关隘。省道寨（河）檀（树岗）公路、南信（阳）叶（集）公路在境内交会，国道106线、大（庆）广（州）高速公路、京九铁路穿境而过。县城北距省城郑州市440公里、信阳市116公里，南距武汉170公里，东距合肥301公里。','新县生态环境优美，被誉为“北国的江南，江南的北国”。新县地处北亚热带向暖温带过渡地带，属大陆性湿润季风气候，四季分明，雨量充沛，光照充足。年平均降水量1313.8毫米，日照时数1742.3小时，相对湿度77%，无霜期243.7天。年平均地表径流量9.73亿立方米，地下水储量3.6亿立方米。新县横跨中国大陆南北植物过渡带，动植物资源丰富，汇集众多珍稀物种。有植物2389种，其中有国家级珍稀、濒危保护植物24种，国家重点保护植物23种，河南省重点保护植物22种。有动物1296种，其中发现中国新记录2种，发现河南省新记录68种；国家一级保护动物3种，国家二级保护动物23种，河南省重点保护动物8种。全县植被覆盖率93%，森林覆盖率71.2%，空气中每立方米负氧离子含量1.4万个以上，有“天然氧吧”之称。新县秀似江南，雄比北国。盛夏时节，山秀水碧，雨雾蒙蒙，小桥人家，稻花飘香，颇具江南风韵；严冬季节，雪花漫舞，冰封群峰，银装素裹，玉树琼枝，尽显北国风光。境内奇观异景众多。自古就有六大名景：“三山夜月”，三峰秀出，青翠浮动，月光晶莹，恍若蓬瀛；“天台远岫”，层岩千仞，山巅如台，云蒸霞披，淮南第一；“五马秋云”，峰插霄汉，晓雾炫奇，白云迷漫，天马行空；“沙窝夕照”，夕阳倚山，峻峰返照，烟霞蒸腾，紫翠绚烂；“风岭晴岚”，朝霞辉映，叠嶂烟消，青天朗日，别一区宇；“赛山樵唱”，樵夫鹤鸣，空谷传音，行云流水，宠辱皆忘。比及当代，景色尤殊。连康山国家级自然保护区，林茂山幽，生物荟萃，奇花异草，香树贵木，珍禽走兽，竞相生长，有“大别山生物王国”之称；金兰山国家森林公园，峰峻壁陡,古木参天，瀑流飞泻，碧湖荡漾,道观凌空,香雾飘带，有“天国乐园”之称；香山湖国家水利风景区，湖水清澈，山色葱绿，轻舟摇曳,香炉日烟,天光水色,交相辉映，有“大别山明珠”之称。','新县是鄂豫皖苏区首府所在地，被称作“红军的故乡，将军的摇篮”。新县人民具有光荣的革命传统,1927年1月，吴焕先（新县箭厂河乡人）等在黄安县紫云区箭厂河（今属新县箭厂河乡）创建了鄂豫皖边区第一支农民革命武装——三堂革命红学，这支武装成为黄麻起义中的攻坚力量，新县由此成为鄂豫皖边区党领导的最早的武装起义——黄麻起义的发祥地；1928年6月，中国工农革命军第七军在光山县柴山保（今属新县陈店乡）创建鄂豫皖边区第一块革命根据地，新县成为大别山脉工农武装割据的最早区域；1928年7月，在柴山保尹家嘴组建鄂豫皖革命根据地第一支红军队伍、红四方面军最早的一支队伍——中国工农红军第十一军三十一师，新县成为中国工农红军的重要诞生地；1931年2月，豫南重镇新集镇被红军攻克后，鄂豫皖苏区党政军首脑机关相继迁入或在此建立，新县成为鄂豫皖苏区首府所在地和政治、经济、军事、文化的中心；1932年10月，红四方面军主力西征川陕后，吴焕先、高敬亭（新县新集镇人）、刘名榜（新县郭家河乡人）先后重建和领导了红二十五军、红二十八军和大别山红色游击队，继续坚持大别山区的革命斗争，新县成为坚持大别山红旗不倒的中心区域；1947年8月，刘（伯承）邓（小平）大军千里跃进大别山后，刘邓首长亲自建立了大别山区第一个县级爱国民主政权——经扶县爱国民主政府，并将野战军政治部、文工团、卫生部及医院设在新县，新县成为刘邓大军的立足地和后方基地。在新县这块红色土地上，先后诞生和养育了红四方面军、红二十五军、红二十八军三支主力红军。中国工农红军第一架飞机“列宁号”从这里飞向蓝天，中华苏维埃区域第一届运动会在这里成功举办，著名红色革命歌曲《八月桂花遍地开》、《三大纪律八项注意》由这里唱遍全中国。当时全县人口不足10万人，牺牲5.5万人。家家有红军，户户有烈士。著名的烈士有王志仁、桂步蟾、吴焕先、高敬亭、叶成焕等。从新县和大别山区走出了349位共和国将军，养育了许世友、李德生、郑维山等43位新县籍将军和50余位省部级以上领导干部，留下了董必武、刘伯承、邓小平、徐向前、李先念、刘华清等老一辈无产阶级革命家的战斗足迹。']}]},{'icon':__webpack_require__(889),'title':'历史沿革','details':[{'type':'image','value':__webpack_require__(890)},{'type':'author','value':'管理员发布 January 21, 2015'},{'type':'content','value':['新县是个古老而又年轻的县份，刘伯承、邓小平亲自改县名。','新县很早就有先民居住,境内有南墩龙山文化遗址、天亮寺新石器遗址、艾洼商周文化遗址、大庙冲商周文化遗址。西周至春秋时期属弦国，后属黄国；战国时属秦弦地，后属楚国；秦统一全国后，北部属九江郡，南部属南郡、衡山郡；两汉时期，属江夏郡；三国时为魏国边关，北部属魏弋阳郡，南部先属魏弋阳郡、后属吴蕲春郡；晋代时，北部属豫州弋阳郡，南部属弋阳郡、西阳郡；南北朝时，北部属豫州弋阳郡、广陵郡等，南部属豫州西阳郡、衡州等；隋唐时，北部为光州光山县、仙居县，南部属黄州麻城县、亭州麻城县；宋代时，北部属光州军光山县，南部属黄州麻城县；元朝时，北部属汝宁府光山县，南部属黄州路麻城县；明朝时，北部属河南布政司汝宁府光山县，南部属湖广布政司黄州府麻城县，西南部属黄州府黄安县；清代时，北部属河南布政司汝宁府光山县，南部属湖北布政司黄州府麻城县，西南部属黄州府黄安县；民国初期，北部为河南省光山县，南部属湖北省武汉黄德道，后改为鄂东道、江汉道。1933年10月，国民党政府为加强对鄂豫皖边区的统治，析光山、黄安、麻城各一部，以国民党河南省政府主席刘峙的字“经扶”为县名，以新集镇为治所，设置经扶县，隶属潢川专区,后属河南省第九行政督察区。1947年8月28日，刘邓大军六纵一部攻克新集后，设置经扶县爱国民主政府。12月，根据刘伯承、邓小平提议，改“经扶县”为“新县”，意即人民获得新生，过上幸福生活。1949年10月,中华人民共和国成立后,属河南省潢川专区。1952年12月，属河南省信阳专区。1998年7月，撤销信阳地区，设置地级信阳市，新县随属信阳市。2014年，全县辖5个镇、10个乡，1个管理区，1个街道办（正在筹建）：新集镇、沙窝镇、吴陈河镇、苏河镇、八里畈镇、浒湾乡、周河乡、陡山河乡、千斤乡、卡房乡、郭家河乡、陈店乡、箭厂河乡、泗店乡、田铺乡，香山湖管理区，金兰山街道办，县人民政府驻新集镇朝阳路169号。']}]},{'icon':__webpack_require__(891),'title':'地理环境','details':[{'type':'image','value':__webpack_require__(892)},{'type':'author','value':'管理员发布 January 21, 2015'},{'type':'title','value':'地理位置'},{'type':'content','value':['地理位置新县位于北纬31°28\'～31°46\'，东经114°33\'～115°12\'之间。处河南省南端,大别山腹地，鄂豫皖三省六县结合部。东与商城县接壤，南、西面分别与湖北省麻城市、红安县、大悟县及本省罗山县毗邻，北与光山县缘连。京九铁路、312国道和正在建设的大（大庆）广（广州）高速公路穿境而过，东距合肥240公里，南距武汉160公里，北距郑州390公里，素有“三省通衢”、“中原南门”之称，跨长江、淮河两大流域，境域东西长61.6公里，南北宽40.7公里。水文气候新县属亚热带北部大陆性季风气候，具有亚热带过渡性特征，四季分明。年平均气温15.2℃，积温5524℃，年均降水1277.5毫米，平均日照2007.7小时，无霜期222天左右。土壤分为三个土类，七个亚类，以黄棕壤土分布最广，面积最大。山地宜林，畈田宜稻麦、油菜等农作物生长。境内有长江、淮河两大流域六大水系，流域面积1574平方公里，河网密度为0.6公里/平方公里。','地形地貌新县境内层峦叠嶂，河溪纵横，有山地、丘陵、冲积河谷和堆积凹谷。平均海拔350米，相对高差951米，海拔700米以上的山峰46座，最高峰田铺乡黄毛尖海拔1011米，为大别山第三高峰，大小河流92条。大别山主脉东——西南向横贯县境中部，将全县分为地势较为倾斜的两部分，南属长江流域，北属淮河流域。自然地理特点为“八山一水一分田”。','新县是个以林业为主的山区县，被形容为“七山一水一分田，一分道路和庄园”。新县的大地构造单元属大别山系淮阳地盾的组成部分，古生代始逐渐由海洋隆起，褶皱成山系。县境轮廓近似长方形，大别山主脉经境内中间横贯东西，形似屋脊状，脊背中又有东、中、西三个高峰区，构成W形地势。由大别山主脉形成江淮分水岭，岭南属长江流域，岭北属淮河流域。境内山峦起伏连绵，峰高谷深，溪河交叉。平均海拔350米。最高点是县南部的黄毛尖，海拔1011米；最低处是潢河出境处，海拔60米；相对高差为951米。有6大水系，大、小河流92条，其中源于境内的较大河流有潢河、白露河、寨河、竹竿河、倒水河和举水河。全县5公里以上河流总长度684公里，河网密度为每平方公里06公里。以海拔高度和相对高差划分，属深山区的面积为155.15万亩,占山地总面积的66.2%;浅山区的面积79.2万亩,占33.8%。相对高差在500米以上的乡镇有14个，500米以下的有3个。新县农业以林业为主。2005年，耕地面积19.8万亩，占全县总面积的8.2%；山场面积188万亩，占77.8%；水域面积6.73万亩,占2.78%。林业用地162万亩，占67%，人均5.86亩。全县有林地159万亩，占林业用地的98%，其中用材林79万亩，经济林45万亩，薪炭林11万亩，防护林21万亩，活立木蓄积量181万立方米。全县有国营林场22个，村组办林场700多个，户或联户办林场1000多个。']},{'type':'title','value':'气候'},{'type':'content','value':'新县生态环境优美，被誉为“北国的江南，江南的北国”。新县地处北亚热带向暖温带过渡地带，属大陆性湿润季风气候，四季分明，雨量充沛，光照充足。年平均降水量1313.8毫米，日照时数1742.3小时，相对湿度77%，无霜期243.7天。年平均地表径流量9.73亿立方米，地下水储量3.6亿立方米。'}]},{'icon':__webpack_require__(893),'title':'民风民俗','details':[{'type':'image','value':__webpack_require__(894)},{'type':'author','value':'管理员发布 January 21, 2015'},{'type':'title','value':'生活习俗'},{'type':'subTitle','value':'服装'},{'type':'content','value':'建国前，富裕人家在冬季除穿棉短袄、棉长袄、衫外，还预备皮坎背、皮毛短袄、长袄；穷人多数只穿土布棉袄、夹裤过冬，极穷者只以夹衣单裤御寒。高温季节，富者多穿麻纱、丝绸；穷者男性多裸胸露背，下着土布短裤或长裤，赤脚，背披一条蓝花土布汗巾。女性，富者冬季裹轻裘，春秋季着绸缎，夏季着丝麻；穷者一年四季只着粗布衣衫。'},{'type':'subTitle','value':'饰戴'},{'type':'content','value':'建国前，青年女性头饰、手饰多银器、玉器，诸如银发钎、玉器钻、银耳环、银手镯、银戒指之类装饰品。富豪之家的妇女除有大量银、玉之类饰品外，还拥有金、珠、玛瑙、翡翠等精工饰戴品。女性从不戴帽，只搭蓝色头巾。有身份的男性多头戴瓜皮帽、礼帽或毡帽。殷实之家的男人们还手戴金镏子、银镏子之类。平民百姓冬季多头戴“狗钻窿”线帽（即线卫生帽）御寒。'},{'type':'subTitle','value':'发式'},{'type':'content','value':'建国前，本县男性多剃“和尚头”，在古稀老人中，亦有一些扎辫子和留“东洋发”的。少数官绅、商贾和“诗书”之家，男性多蓄大背头或分发。女性发型以年岁大小，婚否而各有不同，女童多扎两只朝天叉；少年扎一条发辫；进入青年期后，前蓄“刘海”，后拖长辫，出嫁时，即去掉“刘海”挽发带簪，入中年以后，挽发后再戴上网套。'},{'type':'subTitle','value':'饮食'},{'type':'content','value':'本县人以食大米为主，麦面、红薯、豆类次之。由于处于深山区，山高水淡，劳动强度大，本县人惯于吃干饭，少喝稀粥，喜大油大荤。本县人对吃菜比较讲究，且多以熟菜为主。待客之餐，往往是菜肴满桌。即使是贫寒之家，也要借钱拉债殷勤待客，待客除三餐之外，还要过“晌午”和过“晚上”。隆重时待客酒席名曰“双抬席”，即每样菜肴均是双份，也叫“吃一席看一席”。新县饮茶之风尤盛，可谓全省之冠。普通人家常年储存茶叶少则10数斤，多则数十斤，男女老少均以茶为必要饮料。可以无油少盐而不可无茶。箭厂河、陈店、田铺、卡房、泗店等乡更视饮茶为第一要事。人们早晨起床后的第一件事是烧开水沏茶、喝茶，然后才开始劳作。待客之茶有许多讲究，宾客进门首先捧上一杯清茶，客人边喝，主人边斟，一直待客人倒掉杯中茶脚方止。不知其风俗者，往往被茶水灌得手足无措而不知其所。'},{'type':'subTitle','value':'居住'},{'type':'content','value':['盖新房为居家一大盛事，亲朋、好友、邻里须来相帮并致恭贺礼。建新房的讲究名目繁多，首先要请风水先生选择地基，然后酌定黄道吉破土动工。屋梁要选择红香椿或椿木制作，还要焚香烧纸，披红挂彩，名曰“暖梁”。梁大多是偷砍别人的活立木，名曰“偷梁”。“偷梁”之风始于何时，无从查考，但偷砍他人树木做梁，被视为天经地义，无可非议之事。上梁之时，一人唱彩，众人呼和，边唱边撒五谷杂粮和麦、米面制作的各种形状的馍馍，名曰“撒梁粑”。湾里的邻居多在上梁时前来凑热闹，名“抢粮粑”。','旅行：旧时出远门旅行，需要选择黄道吉日，日子“不好”或“不干净”一律不走，尤以商贾最为讲究。经商人一般选择双日出门，双日又以四、六、八日为佳。单日中逢三、九为凶日。故此，俗有“初三、十三、二十三、姥姥给马不去牵”之说。']},{'type':'title','value':'婚嫁习俗'},{'type':'content','value':'建国前，男女婚配尊从父母之命，媒妁之言，“明媒正娶”。故包办婚姻，买卖婚姻。定“摇窝婚”、“童子婚”、“指腹为婚”的现象极为普遍，并被认为是天经地义的事。婚嫁礼仪繁杂，主要有订婚、换贴、送日子，嫁娶等程序。嫁娶仪式主要有迎亲和送亲两种。迎亲是男子坐轿到女家迎娶，并须向女方家长行初见叩拜礼，然后，女方随轿及其嫁奁到男方完婚。送亲，即男方发轿到女方，而男子并不前往，由女方兄、嫂或至亲送女子到男家完婚。'},{'type':'title','value':'生育习俗'},{'type':'content','value':'本县旧时特别重视生育，多生多育被视为人丁兴旺的盛事，尤其喜欢生男孩，故有“五子登科”、“七子团圆”、“十子全福”等“多子多福”的美好传说。旧例，凡生第一胎婴儿，男方须立即向岳丈家报喜，岳父母将早已准备好的小孩衣物（名曰“毛衫”）、母鸡等交报喜人带回。婴儿“三朝”这天，岳母以探视为名提筐往贺，并告知娘家送“月礼”的具体时间。“三朝”要给小孩起乳名，洗澡穿衣，因此三朝又叫“洗三”。“月礼”一般在婴儿出生后的七至十天内送到男方家里。满月的当天或第二天，产妇抱着婴儿走娘家，名曰“吃满月盐”、产妇坐月期间，禁止到别人家中去，若进了谁家门槛，将被视为不吉利。'},{'type':'title','value':'岁节习俗'},{'type':'subTitle','value':'除夕和春节'},{'type':'content','value':'除夕和春节是一年中相毗连的两个大节。除夕日这天，全家团圆，欢聚一堂，举行丰盛的家宴，祭奠列祖列宗，张贴对联、门神门画，入夜后在祖宗牌位前点烛、焚香直至天明。全家老少围坐火塘说笑，俗称“守岁”，戏之为“守棉袄”。鸡鸣家长沐浴更换新衣、新帽、新鞋袜、礼拜祖宗、天地，迎接财神、喜神、灶神，称之为“接年”。正月初一早晨吃饺子，然后往湾邻拜年，以先尊长，先亲近，后平辈，后疏远为序，一家不漏。初二以后，主要亲朋之间相互走动，一般到正月十三为止。'},{'type':'subTitle','value':'正月十五'},{'type':'content','value':'正月十五官称“元宵节”，也称为“灯节”。农家历来重视元宵节，素有“十五大似年”之说，节日这天，合家团圆，举行早餐宴。早餐抢早，象征早种早收，晚饭吃水饺，黄昏之后，家家户户去坟山给祖先送灯以示纪念，“圆亮”（灯火全部都点着）之后，山坡上灯光成排成行，横竖交错。入夜，各村、各湾组织民间艺人举行玩狮子、划旱船、跑竹马、踩高跷、下地灯、舞彩龙等文艺活动，家家户户彩灯高挂，室内灯火通明，通宵达旦，民间称之为“观灯”。但本县泗店、田铺、箭厂河、陈店等乡只是清明节到老坟上添土插幡、挂纸，无十五送灯之俗。'},{'type':'subTitle','value':'清明'},{'type':'content','value':'清明节日前三天，各家各户即开始祭祖扫墓，给祖茔上坟搭土，戴帽挂纸（闰月年忌上土），以示缅怀，节日早餐多以腊猪头肉为主菜，早起后，各家门楣遍插柳枝，象征春天正式降临。'},{'type':'subTitle','value':'端阳'},{'type':'content','value':'又称端午，五月初五为小，十五为大。端午节早餐吃粽子，并以数个抛撒河、塘、湖、库之中，表示对楚国大诗人屈原的祭奠和怀念。门窗上两角各插艾蒿一根，象征去风避邪。各家还要采集大量的哈蟆叶（车前子）、薄荷、苦艾晒干储存，以备婴儿三朝“洗三”，产妇洗澡，各种风温皮肤病人蒸洗，炙烧之用。中午吃水饺，饮雄黄药酒，菜肴多少不限，饮雄黄洒以示去瘴气避邪气，并象征能避免蚊叮虫咬，蛇、蝎、蜈蚣等毒虫的侵袭。五十年代后期，饮雄黄酒的逐渐减少，现基本绝迹。端阳节也是人们相互交往宴请之节。'},{'type':'title','value':'禁忌'},{'type':'content','value':'“老帅”。冰雹叫“冻子”。'},{'type':'subTitle','value':'器官部位称谓禁忌'},{'type':'content','value':'牙称为“财吊子”。舌头称为“赚头”。耳朵称为“顺风”。'},{'type':'subTitle','value':'日常生活禁忌'},{'type':'content','value':['产妇末满月禁去别人家。出嫁后忌在娘家生孩子。出嫁后忌在娘家过春节和元宵节。同姓同宗男女世代禁忌通婚。生意人每天第一份交易忌赊帐。早晨最忌说“龙”、“虎”、“梦”、“牙”，次为“雾、庙、桥、塔”。生意人早晨忌','以上禁忌，均有特定的场合和时间，早晨，年节禁忌较严；人们在烧窑，砍树、烧炭、挖山、打石、运输、建房、喜庆等场合，严格禁止说犯忌的话。在其他时间和场合，禁忌较少，也不严格。']}]},{'icon':__webpack_require__(895),'title':'民族宗教','details':[{'type':'image','value':__webpack_require__(871)},{'type':'author','value':'管理员发布 January 21, 2015'},{'type':'content','value':'新县地处河南省与湖北省交界，结合了多方文化。有佛教、道教两大宗教，宗教活动场所多处。寺庙遍布全市：玉佛寺、金兰山道观、普济寺、观音洞。'},{'type':'image','value':__webpack_require__(896)},{'type':'content','value':'玉佛寺：人们游历结缘、烧香礼佛、禅修养性，陶冶心境的好去处。'},{'type':'image','value':__webpack_require__(897)},{'type':'content','value':'金兰山：道教圣地，和谐社会也需要道的思想。当您走入金兰山的时候，到处可以看到“道”的思想，道教人物的身影，在这里，您的身心可以得到洗礼。'},{'type':'image','value':__webpack_require__(871)},{'type':'content','value':'普济寺：始建于唐，为佛教天台宗思想奠基人慧思禅师再传弟子驻锡之地。寺建之前，此地有华佗庙。据民间传说，三国时华佗于此治病救人，大孚民信，得香火之享。'}]},{'icon':__webpack_require__(898),'title':'文化艺术','details':[{'type':'image','value':__webpack_require__(899)},{'type':'author','value':'管理员发布 January 21, 2015'},{'type':'title','value':'地灯戏'},{'type':'content','value':'以新县民歌和地方小调为旋律音调，唱词朴素大方，有唱又说，有情节、有内容。演员一般二至三人，伴奏乐器为锣鼓和唢呐。演出场地可大可小，舞台上、农家大院内、广场上均可演出，是新县群众喜闻乐见的艺术形式。'},{'type':'image','value':__webpack_require__(900)},{'type':'title','value':'舞龙'},{'type':'content','value':'俗称玩龙灯，是一种中国的汉族传统民俗文化活动之一。每逢喜庆节日，都会舞龙。舞龙时，龙跟着绣球做各种动，穿插，不断地展示扭、挥、仰、跪、跳、摇等多种姿势。所以以舞龙的方式来祈求平安和丰收就成为全国各地汉族的一种民俗文化。舞龙和舞狮，古时是在一年中的大型节日里面节目之一。'},{'type':'image','value':__webpack_require__(901)},{'type':'title','value':'舞狮'},{'type':'content','value':'是我国优秀的民间艺术。每逢佳节或集会庆典，民间都以舞狮来助兴、舞狮有南北之分，南方以广东的舞狮表演最为有名。狮子是由彩布条制作而成的。每头狮子有两个人合作表演，一人舞头，一人舞尾。表演者在锣鼓音乐下，装扮成狮子的样子，做出狮子的各种形态动作。在表演过程中，舞狮者要以各种招式来表现南派武功，非常富有阳刚之气。'},{'type':'image','value':__webpack_require__(902)},{'type':'title','value':'旱船'},{'type':'content','value':['是汉族民间表演艺术形式之一，逢年过节或庆祝等，陕西、山西、河北境内各地都广为流行，这是一种模拟水中行船的汉族民间舞蹈。 “旱船”是依照船的外观形状制成的木架子。在这种船形木架周围，围缀上绘有水纹的棉布裙或是海蓝色的棉布裙。在船的上面，装饰以红绸、纸花，有的地方还装有彩灯、明镜和其它装饰物，把这只或者十几只船装饰得艳丽不凡。','“旱船”，自然是陆地上的船。乘船者一般是一个人，有时也有双人、四人甚至七人共同乘用一只船的。乘船者所表现的多是姑娘、媳妇，也有扮演其它人物的。']},{'type':'image','value':__webpack_require__(903)},{'type':'title','value':'皮影戏'},{'type':'content','value':'又称“影子戏”或“灯影戏”，是一种以兽皮或纸板做成的人物剪影，在蜡烛或燃烧的酒精等光源的照射下用隔亮布进行演戏，是中国汉族民间广为流传的傀儡戏之一。表演时，艺人们在白色幕布后面，一边用手操纵戏曲人物，一边用当地流行的曲调唱述故事，同时配以打击乐器和弦乐，有浓厚的乡土气息。'},{'type':'image','value':__webpack_require__(904)},{'type':'title','value':'县歌舞团'},{'type':'content','value':'由原县歌舞团改制而成，其根据地方民歌民乐自编自演百余个当地特色曲目，常年开展“唱红歌”，“赛明歌”，流动巡演等文化活动，曾代表河南省参加在江西省举报的全国红色旅游博览会开幕式及红歌会演出，并得到好评。该团丰富多彩的演出节目成为游人和群从的精神食粮。她们那青春的律动，飒爽的英姿，也是道靓丽的风景。'}]}];

